import React from 'react'
import {BrowserRouter as Router,Switch,Route,Link} from 'react-router-dom'

export default function Nav() {
    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark line">
        <div className="col d-flex flex-column">
            <h1 className="masthead-brand mx-auto justify-content-center align-items-center">
                <span>
                    <Link to="/"><a href="/" className="logo">ARISTO</a></Link>
                    </span>
            </h1>
        </div>
        <div className="col d-flex align-items-center flex-column">
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExample08" aria-controls="navbarsExample08" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
        </div>
        <div className="px-5 collapse navbar-collapse justify-content-md-center" id="navbarsExample08">
            <ul className="navbar-nav">
                <li className="nav-item">
                <Link className="nav-link" to="/">ראשי</Link>
                </li>
                <li className="nav-item active">
                    <Link className="nav-link" to="/main">מילון מונחים<span className="sr-only">(current)</span></Link>
                </li>
            </ul>
        </div>
    </nav>
    )
}
